import { defHttp } from '/@/utils/http/axios';
import { BasicColumn } from '/@/components/Table';
import { useRender } from '@/components/Table';
import type { FormProps } from '/@/components/Form/src/types/form';
import { useUserStore } from '/@/store/modules/user';

export enum Api {
  LIST_STATION_TREE_URL = '/baseset/common/list-orgStationTree',
  LIST_STATION_PERSON_URL = '/baseset/common/list-stationPerson',
  CREATE_URL = '/po/ticket/secondworkticket/create',
  UPDATE_URL = '/po/ticket/secondworkticket/update',
  DELETE_URL = '/po/ticket/secondworkticket/delete',
  UPLOAD_FILE_URL = '/po/ticket/secondworkticket/upload-file',
  EXPORT_EXCEL_URL = '/po/ticket/secondworkticket/export-excel',
  GET_URL = '/po/ticket/secondworkticket/get',
  PAGE_URL = '/po/ticket/secondworkticket/page',
  GET_ORG_URL = '/po/ticket/operationticket/org',
  LIST_RECORD_URL = '/po/ticket/secondworkticket/record/list',
}

const dateTimeFormat = 'YYYY-MM-DD HH:mm';

// 获取电站树
export const getTreeList = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_TREE_URL,
      params,
    },
    {},
  );

// 获取电站下的组织人员
export const getStationPerson = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_PERSON_URL,
      params,
    },
    {},
  );

// 添加第二种工作票
export const createData = (params) =>
  defHttp.post(
    {
      url: Api.CREATE_URL,
      params,
    },
    {},
  );

// 修改第二种工作票
export const updateData = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_URL,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return createData(params);
  }
  return updateData(params);
};

// 删除第二种工作票
export const deleteData = (params) =>
  defHttp.delete(
    {
      url: Api.DELETE_URL,
      params,
    },
    {},
  );

// 导出第二种工作票
export const exportExcel = (params) =>
  defHttp.post(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
      responseType: 'blob',
    },
    { isOnlyResult: true },
  );

// 上传第二种工作票文件
export const uploadFile = (params) =>
  defHttp.post(
    {
      url: Api.UPLOAD_FILE_URL,
      params,
    },
    {},
  );

// 获取第二种工作票
export const getData = (params) =>
  defHttp.get(
    {
      url: Api.GET_URL,
      params,
    },
    {},
  );

// 分页获取第二种工作票列表
export const getPage = (params) =>
  defHttp.get(
    {
      url: Api.PAGE_URL,
      params,
    },
    {},
  );

// 获取第二种工作票记录列表
export const getRecordList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_RECORD_URL,
      params,
    },
    {},
  );

// 获取登录用户信息
export const getOrg = (params) =>
  defHttp.get(
    {
      url: Api.GET_ORG_URL,
      params,
    },
    {},
  );

export const columns = (): BasicColumn[] => {
  const result = [
    {
      title: '工作票号',
      dataIndex: 'ticketNumber',
      sorter: true,
      resizable: true,
      width: 200,
    },
    {
      title: '所属公司',
      dataIndex: 'stationOrganName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '电站名称',
      dataIndex: 'stationName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '电站设备名称',
      dataIndex: 'subject',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '工作负责人',
      dataIndex: 'headName',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '计划工作开始时间',
      dataIndex: 'startTime',
      sorter: true,
      resizable: true,
      width: 150,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '计划工作结束时间',
      dataIndex: 'endTime',
      sorter: true,
      resizable: true,
      width: 150,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '执行负责人',
      dataIndex: 'performHeadName',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '实际工作开始时间',
      dataIndex: 'opstartTime',
      sorter: true,
      resizable: true,
      width: 150,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '实际工作结束时间',
      dataIndex: 'opendTime',
      sorter: true,
      resizable: true,
      width: 150,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '检查结论',
      dataIndex: 'result',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: ({ record }) => {
        return record.resultText;
      },
    },
    {
      title: '检查时间',
      dataIndex: 'checkTime',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '创建人',
      dataIndex: 'personMemberName',
      sorter: true,
      resizable: true,
      width: 100,
    },
    {
      title: '创建时间',
      dataIndex: 'fillinDate',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '审批状态',
      dataIndex: 'status',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: ({ text, record }) => {
        return useRender.renderFlowStatus(text, record.statusText);
      },
    },
  ];
  if (useUserStore().isMultiOrganization) {
    return result as BasicColumn[];
  } else {
    const [one, , ...others] = result;
    return [one, ...others] as BasicColumn[];
  }
};

